import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  fetchProductBrands,
  fetchProductById,
  fetchProductCategories,
  fetchProductList,
  addProduct,
  updateProduct,
} from "./ProductListAPI";

const initialState = {
  products: [],
  brands: [],
  categories: [],
  selectedProduct: null,
  status: "idle",
};

// The function below is called a thunk and allows us to perform async logic. It
// can be dispatched like a regular action: `dispatch(incrementAsync(10))`. This
// will call the thunk with the `dispatch` function as the first argument. Async
// code can then be executed and other actions can be dispatched. Thunks are
// typically used to make async requests.
export const fetchProductListAsync = createAsyncThunk(
  "Product/fetchProductList",
  async ([pagination, sorting, filteration]) => {
    const response = await fetchProductList([pagination, sorting, filteration]);

    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchProductCategoriesAsync = createAsyncThunk(
  "Product/fetchProductCategories",
  async () => {
    const response = await fetchProductCategories();

    // The value we return becomes the `fulfilled` action payload

    return response.data;
  }
);

export const fetchProductBrandsAsync = createAsyncThunk(
  "Product/fetchProductBrands",
  async () => {
    const response = await fetchProductBrands();

    // The value we return becomes the `fulfilled` action payload

    return response.data;
  }
);

export const fetchProductByIdAsync = createAsyncThunk(
  "Product/fetchProductById",
  async (id) => {
    const response = await fetchProductById(id);

    // The value we return becomes the `fulfilled` action payload

    return response.data;
  }
);
export const addProductAsync = createAsyncThunk(
  "Product/addProduct",
  async (id) => {
    const response = await addProduct(id);

    // The value we return becomes the `fulfilled` action payload

    return response.data;
  }
);
export const updateProductAsync = createAsyncThunk(
  "Product/updateProduct",
  async (product) => {
    const response = await updateProduct(product);

    // The value we return becomes the `fulfilled` action payload

    return response.data;
  }
);
export const productSlice = createSlice({
  name: "Product",
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    increment: (state) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.value += 1;
    },
    resetSelectedProduct: (state) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.selectedProduct = null;
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(fetchProductListAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchProductListAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.products = action.payload;
      })
      .addCase(fetchProductCategoriesAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchProductCategoriesAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.categories = action.payload.map((obj) => obj.name);
      })
      .addCase(fetchProductBrandsAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchProductBrandsAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.brands = action.payload.map((obj) => obj.name);
      })
      .addCase(fetchProductByIdAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchProductByIdAsync.fulfilled, (state, action) => {
        state.status = "idle";

        state.selectedProduct = action.payload;
      })
      .addCase(addProductAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(addProductAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.products.push(action.payload);
      })
      .addCase(updateProductAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(updateProductAsync.fulfilled, (state, action) => {
        state.status = "idle";
        let index = state.products.findIndex(
          (product) => product.id === action.payload.id
        );
        state.products[index] = action.payload;
      });
  },
});

export const { increment, resetSelectedProduct } = productSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
export const selectProducts = (state) => state.Product.products;
export const selectBrands = (state) => state.Product.brands;
export const selectProductStatus = (state) => state.Product.status;
export const selectCategories = (state) => state.Product.categories;
export const selectProductById = (state) => state.Product.selectedProduct;

export default productSlice.reducer;
