import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  addOrder,
  fetchAllOrders,
  updateOrder,
  fetchOrderById,
} from "./orderApi";

const initialState = {
  orders: [],
  totalOrders: 0,
  currentOrder: null,

  status: "idle",
};

// The function below is called a thunk and allows us to perform async logic. It
// can be dispatched like a regular action: `dispatch(incrementAsync(10))`. This
// will call the thunk with the `dispatch` function as the first argument. Async
// code can then be executed and other actions can be dispatched. Thunks are
// typically used to make async requests.
export const addOrderAsync = createAsyncThunk(
  "order/addOrder",
  async (item) => {
    const response = await addOrder(item);

    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const fetchOrderByIdAsync = createAsyncThunk(
  "order/fetchOrderById",
  async (id) => {
    const response = await fetchOrderById(id);
    return response.data;
  }
);

export const updateOrderAsync = createAsyncThunk(
  "order/updateOrder",
  async (order) => {
    const response = await updateOrder(order);

    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const fetchAllOrdersAsync = createAsyncThunk(
  "order/fetchAllOrders",
  async ([pagination, sorting]) => {
    const response = await fetchAllOrders([pagination, sorting]);

    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const orderSlice = createSlice({
  name: "order",
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    resetCurrentOrder: (state) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.currentOrder = null;
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(addOrderAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(addOrderAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.totalOrders += 1;
        state.orders.push(action.payload);
        state.currentOrder = action.payload;
      })
      .addCase(addOrderAsync.rejected, (state, action) => {
        state.status = "idle";
        alert(action.error.message);
      })
      .addCase(fetchAllOrdersAsync.pending, (state, action) => {
        state.status = "loading";
      })
      .addCase(fetchAllOrdersAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.orders = action.payload[0];
        state.totalOrders = action.payload[1];
      })
      .addCase(fetchAllOrdersAsync.rejected, (state, action) => {
        state.status = "idle";
        alert(action.error.message);
      })
      .addCase(fetchOrderByIdAsync.pending, (state, action) => {
        state.status = "loading";
      })
      .addCase(fetchOrderByIdAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.currentOrder = action.payload;
      })

      .addCase(updateOrderAsync.fulfilled, (state, action) => {
        state.status = "idle";
        const index = state.orders.findIndex(
          (order) => order.id === action.payload.id
        );

        state.orders.splice(index, 1, action.payload);
      })
      .addCase(updateOrderAsync.pending, (state) => {
        state.status = "loading";
      });
  },
});

export const { resetCurrentOrder } = orderSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
export const selectOrders = (state) => state.order.orders;
export const selectTotalOrders = (state) => state.order.totalOrders;
export const selectCurrentOrder = (state) => state.order.currentOrder;

export default orderSlice.reducer;
