import React from "react";
import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  fetchAllOrdersAsync,
  selectOrders,
  selectTotalOrders,
  updateOrderAsync,
} from "../../order/orderSlice";
import {
  ORDERS_PER_PAGE as ITEMS_PER_PAGE,
  discountedPrice,
} from "../../../app/constants";
import {
  ChevronLeftIcon,
  ChevronRightIcon,
  ArrowUpIcon,
  ArrowDownIcon,
  XMarkIcon,
} from "@heroicons/react/20/solid";
function AdminOrders() {
  const orders = useSelector(selectOrders);
  const totalOrders = useSelector(selectTotalOrders);
  const [page, setPage] = useState(1);
  const [start, setStart] = useState(0);
  const [sortParams, setSortParams] = useState({ key: "id", order: "asc" });

  const dispatch = useDispatch();
  useEffect(() => {
    const sorting = { _sort: sortParams.key, _order: sortParams.order };
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE };
    dispatch(fetchAllOrdersAsync([pagination, sorting]));
  }, [dispatch, page, sortParams]);
  const [editId, setEditId] = useState(-1);
  function handleShow() {}
  function handleEdit(e, id) {
    if (+editId === +id) {
      setEditId(-1);
      return;
    }
    setEditId(id);
  }
  function handleStatusChange(e, order) {
    const updatedOrder = {
      ...order,
      status: e.target.value,
      items: order.items.map((item) => ({
        product: item.product.id,
        quantity: item.quantity,
      })),
    };

    dispatch(updateOrderAsync(updatedOrder));
    setEditId(-1);
  }
  function handleSort(e, field) {
    const newSortParams = {
      ...sortParams,
      key: field,
      order:
        sortParams.order === "asc" && sortParams.key === field ? "desc" : "asc",
    };
    setSortParams(newSortParams);
  }
  return (
    <>
      <div className="overflow-x-auto">
        <div className="min-w-screen bg-gray-100 flex justify-center bg-gray-100 font-sans ">
          <div className="w-full ">
            <div className="bg-white shadow-md rounded ">
              <table className="min-w-max w-full table-auto">
                <thead>
                  <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
                    <th
                      className="py-3 px-6 text-left cursor-pointer"
                      onClick={(e) => {
                        handleSort(e, "id");
                      }}
                    >
                      Order Id
                      {sortParams.key === "id" &&
                        (sortParams.order === "asc" ? (
                          <ArrowUpIcon className="h-5 w-5 inline"></ArrowUpIcon>
                        ) : (
                          <ArrowDownIcon className="h-5 w-5 inline"></ArrowDownIcon>
                        ))}
                      {/* {} */}
                    </th>
                    <th className="py-3 px-6 text-left">Items</th>
                    <th
                      className="py-3 px-6 text-center cursor-pointer"
                      onClick={(e) => {
                        handleSort(e, "total");
                      }}
                    >
                      Amount
                      {sortParams.key === "total" &&
                        (sortParams.order === "asc" ? (
                          <ArrowUpIcon className="h-5 w-5 inline"></ArrowUpIcon>
                        ) : (
                          <ArrowDownIcon className="h-5 w-5 inline"></ArrowDownIcon>
                        ))}
                    </th>
                    {/* <th className="py-3 px-6 text-center">ShippingAddress</th> */}
                    <th className="py-3 px-6 text-center">Status</th>
                    <th className="py-3 px-6 text-center">Actions</th>
                  </tr>
                </thead>
                <tbody className="text-gray-600 text-sm font-light">
                  {orders.map((order) => (
                    <tr className="border-b border-gray-200 hover:bg-gray-100">
                      <td className="py-3 px-6 text-left whitespace-nowrap">
                        <div className="flex items-center ">
                          <span className="font-medium">{order.id}</span>
                        </div>
                      </td>
                      <td className="py-3 px-6 text-left">
                        {order.items.map(({ product, quantity }) => (
                          <div className="flex items-center ">
                            <div className=""></div>
                            <img
                              className="w-6 h-6 rounded-full border-gray-200 border transform hover:scale-125 mr-2 "
                              src={product.thumbnail}
                              alt={product.title}
                            />
                            <span className="text-black-700">
                              <strong>{product.title}</strong> - #{quantity} -
                              Rs. {discountedPrice(product)}
                            </span>
                          </div>
                        ))}
                      </td>
                      <td className="py-3 px-6 text-center">
                        <div className="flex items-center font-medium justify-center">
                          Rs. {order.total}
                        </div>
                      </td>
                      {/* <td className="py-3 px-6 text-center">
                        <div className="text-black-900 font-bold">
                          <strong>{order.address.name}</strong>
                        </div>

                        <div>{order.address.email}</div>
                        <div>{order.address.phone}</div>
                        <div>{order.address.street}</div>
                        <div>
                          {order.address.city},{order.address.state},
                          {order.address.country}
                        </div>

                        <div>{order.address.postalCode}</div>
                      </td> */}
                      <td className="py-3 px-6 text-center">
                        {/* {todo to conditionally find status and giving class accordingly} */}
                        {editId !== order.id ? (
                          <span
                            className={`${getColor(
                              order.status
                            )}py-1 px-3 rounded-full text-xs`}
                          >
                            {order.status}
                          </span>
                        ) : (
                          <select
                            name="status"
                            id="status"
                            onChange={(e) => handleStatusChange(e, order)}
                          >
                            <option
                              value="pending"
                              selected={"pending" === order.status}
                            >
                              pending
                            </option>
                            <option
                              value="dispatched"
                              selected={"dispatched" === order.status}
                            >
                              dispatched
                            </option>
                            <option
                              value="delivered"
                              selected={"delivered" === order.status}
                            >
                              delivered
                            </option>
                            <option
                              value="cancelled"
                              selected={"cancelled" === order.status}
                            >
                              cancelled
                            </option>
                          </select>
                        )}
                      </td>
                      <td className="py-3 px-6 text-center">
                        <div className="flex item-center justify-center">
                          <div
                            onClick={(e) => handleShow(e, order.id)}
                            className="w-6 mr-2 transform hover:text-purple-500 hover:scale-110"
                          >
                            <svg
                              xmlns="http://www.w3.org/2000/svg"
                              fill="none"
                              viewBox="0 0 24 24"
                              stroke="currentColor"
                            >
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                              />
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"
                              />
                            </svg>
                          </div>
                          <div
                            onClick={(e) => handleEdit(e, order.id)}
                            className="w-6 mr-2 transform hover:text-purple-500 hover:scale-110"
                          >
                            {editId === order.id ? (
                              <XMarkIcon />
                            ) : (
                              <svg
                                xmlns="http://www.w3.org/2000/svg"
                                fill="none"
                                viewBox="0 0 24 24"
                                stroke="currentColor"
                              >
                                <path
                                  strokeLinecap="round"
                                  strokeLinejoin="round"
                                  strokeWidth={2}
                                  d="M15.232 5.232l3.536 3.536m-2.036-5.036a2.5 2.5 0 113.536 3.536L6.5 21.036H3v-3.572L16.732 3.732z"
                                />
                              </svg>
                            )}
                          </div>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
      <Pagination
        page={page}
        setPage={setPage}
        start={start}
        setStart={setStart}
        totalItems={totalOrders}
      />
    </>
  );
}

function Pagination({ page, setPage, start, setStart, totalItems }) {
  function handlePrevClick() {
    if (page > 1) {
      if (page % 5 === 1) {
        setStart(page - ITEMS_PER_PAGE);
        setPage(page - 1);
      } else {
        setPage(page - 1);
      }
    } else {
      alert("Not Allowed");
    }
  }

  function handleNextClick() {
    const lastPage = Math.ceil(totalItems / ITEMS_PER_PAGE);
    if (page < lastPage) {
      if (page % 5 === 0) {
        setStart(page);
        setPage(page + 1);
      } else {
        setPage(page + 1);
      }
    } else {
      alert("Not Allowed");
    }
  }

  return (
    <div className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
      <div className="flex flex-1 justify-between sm:hidden">
        <div
          onClick={handlePrevClick}
          className="relative inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Previous
        </div>
        <div
          onClick={handleNextClick}
          className="relative ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Next
        </div>
      </div>
      <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
        <div>
          <p className="text-sm text-gray-700">
            Showing{" "}
            <span className="font-medium">
              {(page - 1) * ITEMS_PER_PAGE + 1}
            </span>{" "}
            to{" "}
            <span className="font-medium">
              {Math.min(page * ITEMS_PER_PAGE, totalItems)}
            </span>{" "}
            of <span className="font-medium">{totalItems}</span> results
          </p>
        </div>
        <div>
          <nav
            className="isolate inline-flex -space-x-px rounded-md shadow-sm"
            aria-label="Pagination"
          >
            <div
              onClick={handlePrevClick}
              className="relative inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
            >
              <span className="sr-only">Previous</span>
              <ChevronLeftIcon className="h-5 w-5" aria-hidden="true" />
            </div>
            {/* Current: "z-10 bg-indigo-600 text-white focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600", Default: "text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:outline-offset-0" */}
            {Array.from({
              length: Math.min(
                Math.ceil(
                  (totalItems - start * ITEMS_PER_PAGE) / ITEMS_PER_PAGE
                ),
                5
              ),
            }).map((el, index) =>
              page === start + index + 1 ? (
                <div
                  key={start + index + 1}
                  aria-current="page"
                  className="relative z-10 inline-flex items-center bg-indigo-600 px-4 py-2 text-sm font-semibold text-white focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                >
                  {start + index + 1}
                </div>
              ) : (
                <div
                  key={start + index + 1}
                  onClick={() => {
                    setPage(start + index + 1);
                  }}
                  className="cursor-pointer relative inline-flex items-center px-4 py-2 text-sm font-semibold text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
                >
                  {start + index + 1}
                </div>
              )
            )}

            <div
              onClick={handleNextClick}
              className="relative inline-flex items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
            >
              <span className="sr-only">Next</span>
              <ChevronRightIcon className="h-5 w-5" aria-hidden="true" />
            </div>
          </nav>
        </div>
      </div>
    </div>
  );
}
function getColor(status) {
  switch (status) {
    case "pending":
      return " bg-purple-200 text-purple-600 ";
    case "dispatched":
      return " bg-yellow-200 text-yellow-600 ";
    case "delivered":
      return " bg-green-200 text-green-600 ";
    default:
      return " bg-red-200 text-red-600 ";
  }
}
export default AdminOrders;
