import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link, Navigate } from "react-router-dom";
import { useForm } from "react-hook-form";
import { addOrderAsync, selectCurrentOrder } from "../order/orderSlice";
import { selectCartItems } from "../cart/cartSlice";
import { updateItemQuantityAsync, removeItemAsync } from "../cart/cartSlice";
import { updateUserAsync } from "../user/userSlice";
import { discountedPrice } from "../../app/constants";
import { selectUserInfo } from "../user/userSlice";

// import { v4 as uuidv4 } from "uuid";
const deliveryOptions = [
  {
    id: 1,
    name: "Blue Dart",
    minDays: 2,
    maxDays: 5,
  },

  {
    id: 2,
    name: "Red Dart",
    minDays: 3,
    maxDays: 7,
  },
];

const paymentMethods = [
  {
    type: "Cash On Delivery(COD)",
    id: 1,
  },
  { type: "Card", id: 2 },
];

function CheckOut() {
  //todo
  //1st address can be selected only
  const products = useSelector(selectCartItems);
  const dispatch = useDispatch();
  const user = useSelector(selectUserInfo);
  const currentOrder = useSelector(selectCurrentOrder);

  const Addresses = user.addresses;
  const total = products.reduce((sum, item) => {
    return sum + discountedPrice(item.product) * item.quantity;
  }, 0);

  const [shippingMethod, setShippingMethod] = useState(null);
  const [paymentMethod, setPaymentMethod] = useState(null);
  const [addressIndex, setAddressIndex] = useState(0);

  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm();
  const onSubmit = (address) => {
    const updatedUser = { ...user, addresses: [...user.addresses, address] };
    dispatch(updateUserAsync(updatedUser));
    reset();
  };

  const handleQuantityChange = (e, item) => {
    const updatedItem = { ...item, quantity: +e.target.value };
    dispatch(updateItemQuantityAsync(updatedItem));
  };

  const handleRemove = (e, item) => {
    dispatch(removeItemAsync(item));
  };

  const handleAddressChange = (e) => {
    setAddressIndex(+e.target.value);
  };
  const handleShippingMethodChange = (e) => {
    setShippingMethod(e.target.value);
  };
  const handlePaymentMethodChange = (e) => {
    setPaymentMethod(e.target.value);
  };

  const handleOrderPlace = async (e) => {
    e.preventDefault();
    if (addressIndex >= 0 && shippingMethod && paymentMethod) {
      const order = {
        items: products.map((item) => ({
          product: item.product.id,
          quantity: item.quantity,
        })),
        userId: user.id,
        address: Addresses[addressIndex],
        total,
        shippingMethod,
        paymentMethod,
        status: "pending",
      };
      dispatch(addOrderAsync(order));
    } else {
      alert("Details Incomplete");
    }
  };
  return (
    <>
      {currentOrder && currentOrder.paymentMethod === "Card" && (
        <Navigate to={`/payment`}></Navigate>
      )}
      {currentOrder &&
        currentOrder.paymentMethod === "Cash On Delivery(COD)" && (
          <Navigate to={`/order-success/${currentOrder.id}`}></Navigate>
        )}

      {products.length <= 0 && <Navigate to="/"></Navigate>}
      <div class="flex flex-col items-center border-b bg-white py-4 sm:flex-row sm:px-10 lg:px-20 xl:px-32">
        <Link to="/" class="text-3xl font-bold text-gray-800">
          Shopify
        </Link>
        <div class="mt-4 py-2 text-xs sm:mt-0 sm:ml-auto sm:text-base">
          <div class="relative">
            <ul class="relative flex w-full items-center justify-between space-x-2 sm:space-x-4">
              <li class="flex items-center space-x-3 text-left sm:space-x-4">
                <Link
                  to="/"
                  class="flex cursor-pointer h-6 w-6 items-center justify-center rounded-full bg-emerald-200 text-xs font-semibold text-emerald-700"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    class="h-4 w-4"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                    stroke-width="2"
                  >
                    <path
                      stroke-linecap="round"
                      stroke-linejoin="round"
                      d="M5 13l4 4L19 7"
                    />
                  </svg>
                </Link>
                <span class="font-semibold text-gray-900">Shop</span>
              </li>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="h-4 w-4 text-gray-400"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                stroke-width="2"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M9 5l7 7-7 7"
                />
              </svg>
              <li class="flex items-center space-x-3 text-left sm:space-x-4">
                <Link
                  to="/checkout"
                  class="flex cursor-pointer h-6 w-6 items-center justify-center rounded-full bg-gray-600 text-xs font-semibold text-white ring ring-gray-600 ring-offset-2"
                  href="#"
                >
                  2
                </Link>

                <span class="font-semibold text-gray-900">Shipping</span>
              </li>
              <svg
                xmlns="http://www.w3.org/2000/svg"
                class="h-4 w-4 text-gray-400"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                stroke-width="2"
              >
                <path
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M9 5l7 7-7 7"
                />
              </svg>
              <li class="flex items-center space-x-3 text-left sm:space-x-4">
                <Link class="flex cursor-not-allowed   disabled h-6 w-6 items-center justify-center rounded-full bg-gray-400 text-xs font-semibold text-white">
                  3
                </Link>
                <span class="font-semibold  text-gray-500">Payment</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="grid  sm:px-10 lg:grid-cols-1lg:px-20 xl:px-32">
        <div class="px-12 pt-8 ">
          <p class="text-xl font-medium">Order Summary</p>
          <p class="text-gray-400">
            Check your items. And select a suitable shipping method.
          </p>
          <div className="mt-8">
            <div className="flow-root">
              <ul className="-my-6 divide-y divide-gray-200">
                {products.map((item) => (
                  <li key={item.product.id} className="flex py-6">
                    <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                      <img
                        src={item.product.thumbnail}
                        alt={item.product.title}
                        className="h-full w-full object-cover object-center"
                      />
                    </div>

                    <div className="ml-4 flex flex-1 flex-col">
                      <div>
                        <div className="flex justify-between text-base font-medium text-gray-900">
                          <h3>
                            <a href={item.product.href}>{item.product.title}</a>
                          </h3>
                          <p className="ml-4">
                            Rs. {discountedPrice(item.product)}
                          </p>
                        </div>
                        <p className="mt-1 text-sm text-gray-500">
                          {item.product.color}
                        </p>
                      </div>
                      <div className="flex flex-1 items-end justify-between text-sm">
                        <p className="text-gray-500">
                          Qty{" "}
                          <span>
                            <select
                              onChange={(e) => {
                                handleQuantityChange(e, item);
                              }}
                              className="border rounded-md px-2 py-1 focus:outline-none focus:border-blue-500 hover:border-blue-300"
                            >
                              <option value="1" selected={1 === item.quantity}>
                                1
                              </option>
                              <option value="2" selected={2 === item.quantity}>
                                2
                              </option>
                              <option value="3" selected={3 === item.quantity}>
                                3
                              </option>
                              <option value="4" selected={4 === item.quantity}>
                                4
                              </option>
                            </select>
                          </span>
                        </p>

                        <div className="flex">
                          <button
                            type="button"
                            onClick={(e) => handleRemove(e, item.product)}
                            className="font-medium text-indigo-600 hover:text-indigo-500"
                          >
                            Remove
                          </button>
                        </div>
                      </div>
                    </div>
                  </li>
                ))}
              </ul>
            </div>
          </div>
          <div className=" mt-10 py-6 ">
            <div className="flex justify-between text-base font-medium text-gray-900">
              <p>Subtotal</p>
              <p>
                Rs.
                {products.reduce((sum, item) => {
                  return sum + discountedPrice(item.product) * item.quantity;
                }, 0)}
              </p>
            </div>

            <p className="mt-0.5 text-sm text-gray-500">
              Shipping and taxes calculated later
            </p>
          </div>

          {/* <div class="relative">
              <input
                class="peer hidden"
                id="radio_2"
                type="radio"
                name="radio"
                checked
              />
              <span class="peer-checked:border-gray-700 absolute right-4 top-1/2 box-content block h-3 w-3 -translate-y-1/2 rounded-full border-8 border-gray-300 bg-white"></span>
              <label
                class="peer-checked:border-2 peer-checked:border-gray-700 peer-checked:bg-gray-50 flex cursor-pointer select-none rounded-lg border border-gray-300 p-4"
                for="radio_2"
              >
                <img
                  class="w-14 object-contain"
                  src="/images/oG8xsl3xsOkwkMsrLGKM4.png"
                  alt=""
                />
                <div class="ml-0">
                  <span class="mt-2 font-semibold">Fedex Delivery</span>
                  <p class="text-slate-500 text-sm leading-6">
                    Delivery: 2-4 Days
                  </p>
                </div>
              </label>
            </div> */}
        </div>

        <div class="mt-20 bg-gray-50 px-12 pt-8 ">
          <p class="text-xl font-semibold leading-6 text-gray-900">
            Billing Details
          </p>

          <form noValidate onSubmit={handleSubmit(onSubmit)}>
            <div className="space-y-12">
              <div className=" pb-0">
                <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                  <div className="sm:col-span-3">
                    <label
                      htmlFor="first-name"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      Name
                    </label>
                    <div className="mt-2">
                      <input
                        type="text"
                        name="name"
                        id="name"
                        autoComplete="given-name"
                        className="px-4 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        {...register("name", {
                          required: "*name is required",
                          // pattern: {
                          //   value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                          //   message: "*Invalid email address",
                          // },
                        })}
                      />
                      {errors.name && (
                        <p className="text-red-500">{errors.name.message}</p>
                      )}
                    </div>
                  </div>

                  <div className="sm:col-span-3">
                    <label
                      htmlFor="first-name"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      Phone No.
                    </label>
                    <div className="mt-2">
                      <input
                        type="tel"
                        name="phone"
                        id="phone"
                        autoComplete="given-phone"
                        className=" px-4 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        {...register("phone", {
                          required: "*phone no. is required",
                          pattern: {
                            value:
                              /^\s*(?:\+?(\d{1,3}))?[-. (]*(\d{3})[-. )]*(\d{3})[-. ]*(\d{4})(?: *x(\d+))?\s*$/,
                            message: "*Invalid Phone No.",
                          },
                        })}
                      />
                      {errors.phone && (
                        <p className="text-red-500">{errors.phone.message}</p>
                      )}
                    </div>
                  </div>

                  <div className="sm:col-span-4">
                    <label
                      htmlFor="email"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      Email address
                    </label>
                    <div className="mt-2">
                      <input
                        id="email"
                        name="email"
                        type="email"
                        autoComplete="email"
                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        {...register("email", {
                          required: "*email is required",
                          pattern: {
                            value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                            message: "*Invalid email address",
                          },
                        })}
                      />
                      {errors.email && (
                        <p className="text-red-500">{errors.email.message}</p>
                      )}
                    </div>
                  </div>

                  <div className="sm:col-span-3">
                    <label
                      htmlFor="country"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      Country
                    </label>
                    <div className="mt-2">
                      <select
                        id="country"
                        name="country"
                        autoComplete="country-name"
                        className="block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:max-w-xs sm:text-sm sm:leading-6"
                        {...register("country", {
                          required: "*country is required",
                        })}
                      >
                        <option>United States</option>
                        <option>Canada</option>
                        <option>Mexico</option>
                      </select>
                      {errors.country && (
                        <p className="text-red-500">{errors.country.message}</p>
                      )}
                    </div>
                  </div>

                  <div className="col-span-full">
                    <label
                      htmlFor="street-address"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      Street address
                    </label>
                    <div className="mt-2">
                      <input
                        type="text"
                        name="street-address"
                        id="street-address"
                        autoComplete="street-address"
                        className="px-4 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        {...register("street", {
                          required: "*street is required",
                        })}
                      />
                      {errors.street && (
                        <p className="text-red-500">{errors.street.message}</p>
                      )}
                    </div>
                  </div>

                  <div className="sm:col-span-2 sm:col-start-1">
                    <label
                      htmlFor="city"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      City
                    </label>
                    <div className="mt-2">
                      <input
                        type="text"
                        name="city"
                        id="city"
                        autoComplete="address-level2"
                        className="px-4 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        {...register("city", {
                          required: "*city is required",
                        })}
                      />
                      {errors.city && (
                        <p className="text-red-500">{errors.city.message}</p>
                      )}
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label
                      htmlFor="region"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      State / Province
                    </label>
                    <div className="mt-2">
                      <input
                        type="text"
                        name="region"
                        id="region"
                        autoComplete="address-level1"
                        className=" px-4 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        {...register("state", {
                          required: "*state is required",
                        })}
                      />
                      {errors.state && (
                        <p className="text-red-500">{errors.state.message}</p>
                      )}
                    </div>
                  </div>

                  <div className="sm:col-span-2">
                    <label
                      htmlFor="postal-code"
                      className="block text-sm font-medium leading-6 text-gray-900"
                    >
                      ZIP / Postal code
                    </label>
                    <div className="mt-2">
                      <input
                        type="text"
                        name="postalCode"
                        id="postalCode"
                        autoComplete="postal-code"
                        className=" px-4 block w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                        {...register("postalCode", {
                          required: "*postal-code is required",
                        })}
                      />
                      {errors.postalCode && (
                        <p className="text-red-500">
                          {errors.postalCode.message}
                        </p>
                      )}
                    </div>
                  </div>
                </div>
                <div className="mt-10 flex items-center justify-end gap-x-6">
                  <button
                    type="button"
                    onClick={() => reset()}
                    className="text-sm font-semibold leading-6 text-gray-900"
                  >
                    Reset
                  </button>
                  <button
                    type="submit"
                    className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                  >
                    Add Address
                  </button>
                </div>
              </div>
            </div>
          </form>

          <div></div>

          <form>
            <div className="border-b border-gray-900/10 pb-12">
              <div className="mt-10 space-y-10 ">
                <fieldset className="border-b border-gray-900/10 py-10">
                  <legend className="text-xl font-semibold leading-6 text-gray-900">
                    Choose Address for Delivery
                  </legend>
                  {/* stacked layout */}
                  <ul className="divide-y divide-gray-100">
                    {Addresses.map((address, index) => (
                      <li key={index} className="gap-x-6 py-5">
                        <div class="relative">
                          <input
                            class="peer hidden"
                            id={`radio_address${index}`}
                            type="radio"
                            name="address"
                            value={index}
                            onChange={handleAddressChange}
                          />
                          <span class="peer-checked:border-gray-700 absolute right-4 top-1/2 box-content block h-3 w-3 -translate-y-1/2 rounded-full border-8 border-gray-300 bg-white"></span>
                          <label
                            class="peer-checked:border-2 peer-checked:border-gray-700 peer-checked:bg-gray-50 flex cursor-pointer select-none rounded-lg border border-gray-300 p-4"
                            for={`radio_address${index}`}
                          >
                            {/* <span class="mt-2 font-semibold">
                                {address.street}
                              </span>
                              <p class="text-slate-500 text-sm leading-6">
                                {address.city}
                              </p> */}

                            <div className="flex min-w-0 gap-x-4 sm:ml-6">
                              <div className="min-w-0 flex-auto">
                                <p className="text-l font-semibold leading-6 text-gray-900">
                                  {address.name}
                                </p>
                                <p className="text-sm font-semibold leading-5 text-gray-500">
                                  {address.phone}
                                </p>
                                <p className="text-sm font-semibold leading-5 text-gray-500">
                                  {address.email}
                                </p>
                                <p className="text-sm font-semibold leading-5 text-gray-500">
                                  {address.street}
                                </p>
                                <div>
                                  <span className="text-sm font-semibold leading-6 text-gray-900">
                                    {address.city}
                                  </span>
                                  <span>,</span>
                                  <span className="text-sm font-semibold leading-6 text-gray-900">
                                    {address.state}
                                  </span>
                                  <span>,</span>
                                  <span className="text-sm font-semibold leading-6 text-gray-900">
                                    {address.country}
                                  </span>
                                </div>

                                <p className="text-sm leading-6 text-gray-900">
                                  {address.postalCode}
                                </p>
                              </div>
                            </div>
                            <div className="hidden shrink-0 sm:flex sm:flex-col sm:items-end"></div>
                          </label>
                        </div>
                      </li>
                    ))}
                  </ul>
                </fieldset>
                <fieldset>
                  <legend className="text-xl font-semibold leading-6 text-gray-900">
                    Shipping Methods
                  </legend>
                  <ul className="divide-y divide-gray-100">
                    {deliveryOptions.map((option) => (
                      <li key={option.id} className=" py-2">
                        <div class="relative mt-6">
                          <input
                            class="peer hidden"
                            id={`radio_delivery${option.id}`}
                            type="radio"
                            name="deliveryOption"
                            value={option.name}
                            onChange={handleShippingMethodChange}
                          />
                          <span class="peer-checked:border-gray-700 absolute right-4 top-1/2 box-content block h-3 w-3 -translate-y-1/2 rounded-full border-8 border-gray-300 bg-white"></span>
                          <label
                            class="peer-checked:border-2 peer-checked:border-gray-700 peer-checked:bg-gray-50 flex cursor-pointer select-none rounded-lg border border-gray-300 p-4"
                            htmlFor={`radio_delivery${option.id}`}
                          >
                            {/* <img
                              class="w-14 object-contain"
                              src="/images/naorrAeygcJzX0SyNI4Y0.png"
                              alt=""
                            /> */}
                            <div class="ml-0 sm:ml-6">
                              <span class="mt-2 font-semibold">
                                {option.name}
                              </span>
                              <p class="text-slate-500 text-sm leading-6">
                                Delivery: {option.minDays}-{option.maxDays} Days
                              </p>
                            </div>
                          </label>
                        </div>
                      </li>
                    ))}
                  </ul>
                </fieldset>
                <fieldset>
                  <legend className="text-xl font-semibold leading-6 text-gray-900">
                    Payment Methods
                  </legend>
                  <ul className="divide-y divide-gray-100">
                    {paymentMethods.map((method, index) => (
                      <li key={method.id} className=" py-2">
                        <div class="relative mt-6 ">
                          <input
                            class="peer hidden"
                            id={`radio_payment_method_${method.id}`}
                            type="radio"
                            name="paymentMethod"
                            value={method.type}
                            onChange={handlePaymentMethodChange}

                            // checked={index === 0 ? true : false}
                          />
                          <span class="peer-checked:border-gray-700 absolute right-4 top-1/2 box-content block h-3 w-3 -translate-y-1/2 rounded-full border-8 border-gray-300 bg-white"></span>
                          <label
                            class="peer-checked:border-2 peer-checked:border-gray-700 peer-checked:bg-gray-50 flex cursor-pointer select-none rounded-lg border border-gray-300 p-4"
                            for={`radio_payment_method_${method.id}`}
                          >
                            {/* <img
                              class="w-14 object-contain"
                              src="/images/naorrAeygcJzX0SyNI4Y0.png"
                              alt=""
                            /> */}
                            <div class="sm:ml-6">
                              <span class="mt-2 font-semibold">
                                {method.type}
                              </span>
                            </div>
                          </label>
                        </div>
                      </li>
                    ))}
                  </ul>
                </fieldset>
              </div>
            </div>
            <button
              onClick={(e) => handleOrderPlace(e)}
              class="mt-4 mb-8 w-full rounded-md bg-indigo-600 px-6 py-3 font-medium text-white"
            >
              Place Order
            </button>
          </form>
        </div>
      </div>
    </>
  );
}

export default CheckOut;
