export function addOrder(order) {
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch(`/api/orders `, {
        method: "POST",
        body: JSON.stringify(order),
        headers: { "content-type": "application/json" },
      });
      const data = await response.json();

      resolve({ data });
    } catch (error) {
      reject({ message: "Failed To Order" });
    }
  });
}

export function getOrdersByUserId(loggedInUserToken) {
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/orders?userId=${loggedInUserToken.id}`);
    const data = await response.json();
    resolve({ data });
  });
}

export function updateOrder(updatedOrder) {
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/orders/${updatedOrder.id}`, {
      method: "PATCH",
      body: JSON.stringify(updatedOrder),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();

    resolve({ data });
  });
}

export function fetchAllOrders([pagination, sorting]) {
  if (sorting._sort === "id" && sorting.order === "asc") {
    sorting = null;
  }
  let queryString = Object.entries({
    ...pagination,
    ...sorting,
  })
    .map(([key, value]) => `${key}=${value}`)
    .join("&");
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/orders/all?${queryString}`);
    const data = await response.json();
    let totalOrders = response.headers.get("X-Total-Count");
    resolve({ data: [data, totalOrders] });
  });
}
export function fetchOrderById(id) {
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/orders/${id}`);
    const data = await response.json();

    resolve({ data });
  });
}
