import { useDispatch, useSelector } from "react-redux";
import { selectUserOrders } from "../userSlice";

import { useEffect } from "react";
import { fetchUserOrdersAsync } from "../userSlice";
import { selectloggedInUserToken } from "../../auth/authSlice";
import { discountedPrice } from "../../../app/constants";

export default function MyOrders() {
  const dispatch = useDispatch();
  const user = useSelector(selectloggedInUserToken);
  const userOrders = useSelector(selectUserOrders);

  useEffect(() => {
    dispatch(fetchUserOrdersAsync(user.id));
  }, [dispatch, user.id]);

  return (
    <>
      {userOrders &&
        userOrders.map((order) => (
          <div className="my-6 ">
            <div className="flex h-full flex-col overflow-y-scroll bg-white shadow-xl">
              <div className="flex-1 overflow-y-auto px-4 py-6 sm:px-6">
                <h1 className="text-4xl font-bold tracking-tight text-gray-900 mb-4">
                  Order ID : #{order.id}
                </h1>
                <h3
                  className={`font-bold ${getColor(
                    order.status
                  )} w-1/6 text-center  mb-4 rounded-md border border-transparent`}
                >
                  {order.status.charAt(0).toUpperCase() + order.status.slice(1)}
                </h3>
                <div className="mt-8">
                  <div className="flow-root">
                    <ul className="-my-6 divide-y divide-gray-200">
                      {order.items.map(({ product, quantity }) => (
                        <li key={product.id} className="flex py-6">
                          <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                            <img
                              src={product.thumbnail}
                              alt={product.title}
                              className="h-full w-full object-cover object-center"
                            />
                          </div>

                          <div className="ml-4 flex flex-1 flex-col">
                            <div>
                              <div className="flex justify-between text-base font-medium text-gray-900">
                                <h3>
                                  <a href={product.href}>{product.title}</a>
                                </h3>
                                <p className="ml-4">
                                  Rs. {discountedPrice(product)}
                                </p>
                              </div>
                              <p className="mt-1 text-sm text-gray-500">
                                {product.color}
                              </p>
                            </div>
                            <div className="flex flex-1 items-end justify-between text-sm">
                              <p className="text-gray-900">Qty : {quantity}</p>
                            </div>
                          </div>
                        </li>
                      ))}
                    </ul>
                  </div>
                </div>
              </div>
              <div className=" py-6 sm:px-6">
                <div className="flex py-2 px-6 justify-between text-base font-medium text-gray-900">
                  <p>Subtotal</p>
                  <p>
                    Rs.
                    {order.items.reduce((sum, item) => {
                      return (
                        sum + discountedPrice(item.product) * item.quantity
                      );
                    }, 0)}
                  </p>
                </div>

                <div className="flex py-2 px-6 justify-between text-base font-medium text-gray-900">
                  <p>Total Items</p>
                  <p>{order.items.length}</p>
                </div>

                <div className="flex py-2 px-6 justify-between text-base font-medium text-gray-900">
                  <p>Payment Method</p>
                  <p>{order.paymentMethod}</p>
                </div>

                <div className="flex py-2 px-6 justify-between text-base font-medium text-gray-900">
                  <p>Shipping Option</p>
                  <p>{order.shippingMethod}</p>
                </div>

                <div className=" py-6 my-6 px-6  text-base font-medium text-gray-900">
                  <h1 className="text-xl font-bold tracking-tight text-gray-900 mb-4">
                    Shipping Address
                  </h1>
                  <div className="flex min-w-0 gap-x-4 border-2 border-gray-600 rounded p-10 pt-5">
                    <div className="min-w-0 flex-auto">
                      <p className="text-l font-semibold leading-6 text-gray-900">
                        {order.address.name}
                      </p>
                      <p className="text-sm font-semibold leading-5 text-gray-500">
                        {order.address.phone}
                      </p>
                      <p className="text-sm font-semibold leading-5 text-gray-500">
                        {order.address.email}
                      </p>
                      <p className="text-sm font-semibold leading-5 text-gray-500">
                        {order.address.street}
                      </p>
                      <div>
                        <span className="text-sm font-semibold leading-6 text-gray-900">
                          {order.address.city}
                        </span>
                        <span>,</span>
                        <span className="text-sm font-semibold leading-6 text-gray-900">
                          {order.address.state}
                        </span>
                        <span>,</span>
                        <span className="text-sm font-semibold leading-6 text-gray-900">
                          {order.address.country}
                        </span>
                      </div>

                      <p className="text-sm leading-6 text-gray-900">
                        {order.address.postalCode}
                      </p>
                    </div>
                  </div>
                </div>

                <div className="mt-6">
                  <p className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"></p>
                </div>
              </div>
            </div>
          </div>
        ))}
    </>
  );
}

function getColor(status) {
  switch (status) {
    case "pending":
      return " bg-purple-200 text-purple-600 ";
    case "dispatched":
      return " bg-yellow-200 text-yellow-600 ";
    case "delivered":
      return " bg-green-200 text-green-600 ";
    default:
      return " bg-red-200 text-red-600 ";
  }
}
