import React, { useEffect } from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useForm } from "react-hook-form";
import { selectUserInfo, updateUserAsync } from "../userSlice";

function Profile() {
  const user = useSelector(selectUserInfo);

  const [showForm, setShowForm] = useState(false);
  const [editForm, setEditForm] = useState({ type: false, index: -1 });

  const {
    register,
    handleSubmit,
    setValue,
    reset,
    formState: { errors },
  } = useForm();

  const dispatch = useDispatch();

  const onAdd = (address) => {
    const userData = { ...user, addresses: [...user.addresses] };
    userData.addresses.push(address);
    dispatch(updateUserAsync(userData));

    reset();
    setShowForm(false);
  };
  const onEdit = (address) => {
    const userData = { ...user, addresses: [...user.addresses] };
    userData.addresses.splice(editForm.index, 1, address);

    dispatch(updateUserAsync(userData));
    reset();
    setShowForm(false);
  };

  useEffect(() => {
    if (editForm.type) {
      Object.entries(user.addresses[editForm.index]).forEach(([key, value]) => {
        setValue(key, value);
      });
    }
  });

  function handleAddAddressClick() {
    setShowForm(true);

    setEditForm({ type: false, index: -1 });
    window.scrollTo({
      top: 0,
      behavior: "smooth", // Use smooth scrolling if available
    });
  }

  function handleEditBtnClick(e, index) {
    setShowForm(true);
    const newEdit = { type: true, index: index };
    setEditForm(newEdit);
    window.scrollTo({
      top: 0,
      behavior: "smooth", // Use smooth scrolling if available
    });
  }

  function handleRemoveBtnClick(e, index) {
    const userData = { ...user, addresses: [...user.addresses] };
    userData.addresses.splice(index, 1);
    if (userData.addresses.length === 0) {
      setShowForm(false);
    }
    dispatch(updateUserAsync(userData));
  }
  return (
    <div className="my-6 ">
      <div className="flex h-full flex-col overflow-y-scroll bg-white shadow-xl">
        <div className="flex-1  overflow-y-auto px-4 py-6 sm:px-6">
          <h1 className="text-4xl text-center font-bold tracking-tight text-gray-900 mb-4">
            Name : {user.name ?? "Guest"}
          </h1>

          <h3 className=" font-bold text-center   mb-4">
            Email : {user.email}
          </h3>

          {user.role === "admin" && (
            <h3 className=" font-bold text-center   mb-4">
              Role : {user.role}
            </h3>
          )}

          {showForm && (
            <form
              noValidate
              onSubmit={
                editForm.type ? handleSubmit(onEdit) : handleSubmit(onAdd)
              }
            >
              <div className="space-y-12">
                <div className=" pb-0">
                  <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
                    <div className="sm:col-span-3">
                      <label
                        htmlFor="first-name"
                        className="block px-2 text-sm font-medium leading-6 text-gray-900"
                      >
                        Name
                      </label>
                      <div className="mt-2">
                        <input
                          type="text"
                          name="name"
                          id="name"
                          autoComplete="given-name"
                          className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          {...register("name", {
                            required: "*name is required",
                            // pattern: {
                            //   value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                            //   message: "*Invalid email address",
                            // },
                          })}
                        />
                        {errors.name && (
                          <p className="text-red-500">{errors.name.message}</p>
                        )}
                      </div>
                    </div>

                    <div className="sm:col-span-3">
                      <label
                        htmlFor="first-name"
                        className="block px-2 text-sm font-medium leading-6 text-gray-900"
                      >
                        Phone No.
                      </label>
                      <div className="mt-2">
                        <input
                          type="tel"
                          name="phone"
                          id="phone"
                          autoComplete="given-phone"
                          className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          {...register("phone", {
                            required: "*phone no. is required",
                            pattern: {
                              value:
                                /^\s*(?:\+?(\d{1,3}))?[-. (]*(\d{3})[-. )]*(\d{3})[-. ]*(\d{4})(?: *x(\d+))?\s*$/,
                              message: "*Invalid Phone No.",
                            },
                          })}
                        />
                        {errors.phone && (
                          <p className="text-red-500">{errors.phone.message}</p>
                        )}
                      </div>
                    </div>

                    <div className="sm:col-span-4">
                      <label
                        htmlFor="email"
                        className="block px-2 text-sm font-medium leading-6 text-gray-900"
                      >
                        Email address
                      </label>
                      <div className="mt-2">
                        <input
                          id="email"
                          name="email"
                          type="email"
                          autoComplete="email"
                          className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          {...register("email", {
                            required: "*email is required",
                            pattern: {
                              value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                              message: "*Invalid email address",
                            },
                          })}
                        />
                        {errors.email && (
                          <p className="text-red-500">{errors.email.message}</p>
                        )}
                      </div>
                    </div>

                    <div className="sm:col-span-3">
                      <label
                        htmlFor="country"
                        className="block px-2 text-sm font-medium leading-6 text-gray-900"
                      >
                        Country
                      </label>
                      <div className="mt-2">
                        <select
                          id="country"
                          name="country"
                          autoComplete="country-name"
                          className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:max-w-xs sm:text-sm sm:leading-6"
                          {...register("country", {
                            required: "*country is required",
                          })}
                        >
                          <option>United States</option>
                          <option>Canada</option>
                          <option>Mexico</option>
                        </select>
                        {errors.country && (
                          <p className="text-red-500">
                            {errors.country.message}
                          </p>
                        )}
                      </div>
                    </div>

                    <div className="col-span-full">
                      <label
                        htmlFor="street-address"
                        className="block px-2 text-sm font-medium leading-6 text-gray-900"
                      >
                        Street address
                      </label>
                      <div className="mt-2">
                        <input
                          type="text"
                          name="street-address"
                          id="street-address"
                          autoComplete="street-address"
                          className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          {...register("street", {
                            required: "*street is required",
                          })}
                        />
                        {errors.street && (
                          <p className="text-red-500">
                            {errors.street.message}
                          </p>
                        )}
                      </div>
                    </div>

                    <div className="sm:col-span-2 sm:col-start-1">
                      <label
                        htmlFor="city"
                        className="block px-2 text-sm font-medium leading-6 text-gray-900"
                      >
                        City
                      </label>
                      <div className="mt-2">
                        <input
                          type="text"
                          name="city"
                          id="city"
                          autoComplete="address-level2"
                          className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          {...register("city", {
                            required: "*city is required",
                          })}
                        />
                        {errors.city && (
                          <p className="text-red-500">{errors.city.message}</p>
                        )}
                      </div>
                    </div>

                    <div className="sm:col-span-2">
                      <label
                        htmlFor="region"
                        className="block px-2 text-sm font-medium leading-6 text-gray-900"
                      >
                        State / Province
                      </label>
                      <div className="mt-2">
                        <input
                          type="text"
                          name="region"
                          id="region"
                          autoComplete="address-level1"
                          className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          {...register("state", {
                            required: "*state is required",
                          })}
                        />
                        {errors.state && (
                          <p className="text-red-500">{errors.state.message}</p>
                        )}
                      </div>
                    </div>

                    <div className="sm:col-span-2">
                      <label
                        htmlFor="postal-code"
                        className="block px-2 text-sm font-medium leading-6 text-gray-900"
                      >
                        ZIP / Postal code
                      </label>
                      <div className="mt-2">
                        <input
                          type="text"
                          name="postalCode"
                          id="postalCode"
                          autoComplete="postal-code"
                          className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                          {...register("postalCode", {
                            required: "*postal-code is required",
                          })}
                        />
                        {errors.postalCode && (
                          <p className="text-red-500">
                            {errors.postalCode.message}
                          </p>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className="mt-10 flex items-center justify-end gap-x-6">
                    <button
                      type="button"
                      onClick={() => reset()}
                      className="text-sm font-semibold leading-6 text-gray-900"
                    >
                      Reset
                    </button>
                    <button
                      type="button"
                      onClick={() => setShowForm(false)}
                      className="text-sm font-semibold leading-6 text-gray-900"
                    >
                      Close
                    </button>
                    <button
                      type="submit"
                      className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
                    >
                      {editForm.type ? "Save Address" : "Add Address"}
                    </button>
                  </div>
                </div>
              </div>
            </form>
          )}

          {user.addresses.length > 0 ? (
            <div className=" py-6 sm:px-6">
              <div className=" py-6 my-6 sm:px-6  text-base font-medium text-gray-900">
                <h1 className="text-3xl font-bold tracking-tight text-gray-900 mb-4">
                  Saved Addresses :
                </h1>
                {user.addresses.map((address, index) => (
                  <div className="flex min-w-0 gap-x-4 border-2 border-gray-600 rounded p-10  pt-5">
                    <div className="min-w-0 flex-auto">
                      <p className="text-l font-semibold leading-6 text-gray-900">
                        {address.name}
                      </p>
                      <p className="text-sm font-semibold leading-5 text-gray-500">
                        {address.phone}
                      </p>
                      <p className="text-sm font-semibold leading-5 text-gray-500">
                        {address.email}
                      </p>
                      <p className="text-sm font-semibold leading-5 text-gray-500">
                        {address.street}
                      </p>
                      <div>
                        <span className="text-sm font-semibold leading-6 text-gray-900">
                          {address.city}
                        </span>
                        <span>,</span>
                        <span className="text-sm font-semibold leading-6 text-gray-900">
                          {address.state}
                        </span>
                        <span>,</span>
                        <span className="text-sm font-semibold leading-6 text-gray-900">
                          {address.country}
                        </span>
                      </div>

                      <p className="text-sm leading-6 text-gray-900">
                        {address.postalCode}
                      </p>
                    </div>
                    <div className="flex flex-col justify-around ">
                      <button
                        className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                        onClick={(e) => handleEditBtnClick(e, index)}
                      >
                        Edit
                      </button>

                      <button
                        className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                        onClick={(e) => handleRemoveBtnClick(e, index)}
                      >
                        Remove
                      </button>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          ) : (
            !showForm && (
              <>
                <h1 className="text-3xl font-bold tracking-tight text-gray-900 mb-4">
                  No Addresses To Show!
                </h1>
                <h3 className=" font-bold  mb-4">
                  You Can Click "Add Address" to add One
                </h3>
              </>
            )
          )}
          {!showForm && (
            <button
              onClick={handleAddAddressClick}
              class="mt-4 mb-8 w-full rounded-md bg-indigo-600 px-6 py-3 font-medium text-white"
            >
              Add Address
            </button>
          )}
        </div>
      </div>
    </div>
  );
}

export default Profile;
