export function addToCart(item) {
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch(`/api/cart`, {
        method: "POST",
        body: JSON.stringify(item),
        headers: { "content-type": "application/json" },
      });
      const data = await response.json();

      resolve({ data });
    } catch (error) {
      reject({ message: "Failed To Add In Cart" });
    }
  });
}

export function getCartItems() {
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/cart`, {
      credentials: "include",
    });

    const data = await response.json();
    resolve({ data });
  });
}

export function updateItemQuantity(item) {
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/cart/${item.id}`, {
      method: "PATCH",
      body: JSON.stringify({
        product: item.product.id,
        userId: item.userId,
        quantity: item.quantity,
      }),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();

    resolve({ data });
  });
}

export function removeItem(item) {
  return new Promise(async (resolve) => {
    await fetch(`/api/cart/${item.id}`, {
      method: "DELETE",
      headers: { "content-type": "application/json" },
    });
    const data = item.id;
    resolve({ data });
  });
}

export function resetCart(user) {
  return new Promise(async (resolve) => {
    const userCartItems = await getCartItems(user);

    for (let item of userCartItems.data) {
      await removeItem(item);
    }
    resolve({ status: "fulfilled" });
  });
}
