import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams, Link, Navigate } from "react-router-dom";
import { selectloggedInUserToken } from "../features/auth/authSlice";
import { resetCartAsync } from "../features/cart/cartSlice";
import {
  fetchOrderByIdAsync,
  resetCurrentOrder,
  selectCurrentOrder,
} from "../features/order/orderSlice";

import { discountedPrice } from "../app/constants";

export default function OrderSuccessPage() {
  const params = useParams();
  const dispatch = useDispatch();
  const user = useSelector(selectloggedInUserToken);
  const order = useSelector(selectCurrentOrder);

  useEffect(() => {
    if (order) {
      dispatch(resetCartAsync(user));
    }
  }, [dispatch, user, order]);

  useEffect(() => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  });
  useEffect(() => {
    dispatch(fetchOrderByIdAsync(params.id));
    return () => dispatch(resetCurrentOrder());
  }, [dispatch, params]);

  return (
    <>
      {!params && <Navigate to="/"></Navigate>}
      {order && (
        <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8">
          <div className="text-center">
            <h1 className="mt-4 text-3xl font-bold tracking-tight text-green-700 sm:text-5xl">
              Your Order Is Successfully Placed !
            </h1>
            <div className="my-6 ">
              <div className="flex h-full flex-col overflow-y-scroll bg-white shadow-xl">
                <div className="flex-1 overflow-y-auto px-4 py-6 sm:px-6">
                  <h1 className="text-4xl font-bold tracking-tight text-gray-900 mb-4">
                    Order ID : #{order.id}
                  </h1>
                  <h3 className=" font-bold  text-red-600 mb-4">
                    Status :{" "}
                    {order.status.charAt(0).toUpperCase() +
                      order.status.slice(1)}
                  </h3>
                  <div className="mt-8">
                    <div className="flow-root">
                      <ul className="-my-6 divide-y divide-gray-200">
                        {order.items.map((item) => (
                          <li key={item.id} className="flex py-6">
                            <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                              <img
                                src={item.product.thumbnail}
                                alt={item.product.title}
                                className="h-full w-full object-cover object-center"
                              />
                            </div>

                            <div className="ml-4 flex flex-1 flex-col">
                              <div>
                                <div className="flex justify-between text-base font-medium text-gray-900">
                                  <h3>
                                    <a href={item.product.href}>
                                      {item.product.title}
                                    </a>
                                  </h3>
                                  <p className="ml-4">
                                    Rs. {discountedPrice(item.product)}
                                  </p>
                                </div>
                                <p className="mt-1 text-sm text-gray-500">
                                  {item.product.color}
                                </p>
                              </div>
                              <div className="flex flex-1 items-end justify-between text-sm">
                                <p className="text-gray-900">
                                  Qty : {item.quantity}
                                </p>
                              </div>
                            </div>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
                <div className=" py-6 sm:px-6">
                  <div className="flex py-2 sm:px-6 justify-between text-base font-medium text-gray-900">
                    <p>Subtotal</p>
                    <p>
                      Rs.
                      {order.items.reduce((sum, item) => {
                        return (
                          sum + discountedPrice(item.product) * item.quantity
                        );
                      }, 0)}
                    </p>
                  </div>

                  <div className="flex py-2 sm:px-6 justify-between text-base font-medium text-gray-900">
                    <p>Total Items</p>
                    <p>{order.items.length}</p>
                  </div>

                  <div className="flex py-2 sm:px-6 justify-between text-base font-medium text-gray-900">
                    <p>Payment Method</p>
                    <p>{order.paymentMethod}</p>
                  </div>

                  <div className="flex py-2 sm:px-6 justify-between text-base font-medium text-gray-900">
                    <p>Shipping Option</p>
                    <p>{order.shippingMethod}</p>
                  </div>

                  <div className=" py-6 my-6 sm:px-6  text-base font-medium text-gray-900">
                    <h1 className="text-xl font-bold tracking-tight text-gray-900 mb-4">
                      Shipping Address
                    </h1>
                    <div className="border-2 border-indigo-600 p-10 pt-4 justify-between min-w-0 ">
                      <div className="text-black-900 font-bold">
                        <strong>{order.address.name}</strong>
                      </div>

                      <div>{order.address.email}</div>
                      <div>{order.address.phone}</div>
                      <div>{order.address.street}</div>
                      <div>
                        {order.address.city},{order.address.state},
                        {order.address.country}
                      </div>

                      <div>{order.address.postalCode}</div>
                    </div>
                  </div>

                  <div className="mt-6">
                    <Link
                      to="/"
                      className="flex items-center justify-center rounded-md border border-transparent bg-indigo-600 px-6 py-3 text-base font-medium text-white shadow-sm hover:bg-indigo-700"
                    >
                      <button
                        onClick={() => {
                          dispatch(resetCurrentOrder());
                        }}
                      >
                        Go Back To Home
                      </button>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </main>
      )}
    </>
  );
}
