import React from "react";
import { createBrowserRouter, RouterProvider } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { useEffect } from "react";
import {
  checkAuthAsync,
  selectloggedInUserToken,
  selectUserChecked,
} from "./features/auth/authSlice.js";
import { getCartItemsAsync } from "./features/cart/cartSlice.js";

import HomePage from "./pages/HomePage.jsx";
import LoginPage from "./pages/LoginPage";
import SignupPage from "./pages/SignupPage";
import ProductDetailsPage from "./pages/ProductDetailsPage.jsx";
import CheckoutPage from "./pages/CheckoutPage.jsx";
import CartPage from "./pages/CartPage.jsx";
import ErrorPage from "./pages/ErrorPage";
import Protected from "./features/auth/components/Protected.jsx";
import ProtectedAdmin from "./features/auth/components/ProtectedAdmin.jsx";
import OrderSuccessPage from "./pages/OrderSuccesPage.jsx";
import MyOrdersPage from "./pages/MyOrdersPage.jsx";
import ProfilePage from "./pages/ProfilePage.jsx";
import { fetchUserInfoAsync } from "./features/user/userSlice.js";
import LogOut from "./features/auth/components/LogOut.jsx";
import AdminHomePage from "./pages/AdminHomePage.jsx";
import AdminProductDetailsPage from "./pages/AdminProductDetailsPage.jsx";

import ForgotPasswordPage from "./pages/ForgotPasswordPage.jsx";
import ResetPasswordPage from "./pages/ResetPasswordPage.jsx";
import AdminProductFormPage from "./pages/AdminProductFormPage.jsx";
import AdminProductEditPage from "./pages/AdminProductEditPage.jsx";
import AdminOrdersPage from "./pages/AdminOrdersPage.jsx";
import PaymentFormPage from "./pages/PaymentFormPage.jsx";
import Contact from "./pages/ContactPage.jsx";
import About from "./pages/AboutPage.jsx";

const router = createBrowserRouter([
  {
    path: "/",
    element: (
      <Protected>
        <HomePage />
      </Protected>
    ),
    errorElement: <ErrorPage />,
  },
  {
    path: "/payment",
    element: (
      <Protected>
        <PaymentFormPage />
      </Protected>
    ),
    errorElement: <ErrorPage />,
  },

  {
    path: "/admin",
    element: (
      <ProtectedAdmin>
        <AdminHomePage />
      </ProtectedAdmin>
    ),
    errorElement: <ErrorPage />,
  },
  {
    path: "/admin/product-form",
    element: (
      <ProtectedAdmin>
        <AdminProductFormPage />
      </ProtectedAdmin>
    ),
    errorElement: <ErrorPage />,
  },

  {
    path: "/admin/product-form/edit/:id",
    element: (
      <ProtectedAdmin>
        <AdminProductEditPage />
      </ProtectedAdmin>
    ),
    errorElement: <ErrorPage />,
  },

  {
    path: "/admin/orders",
    element: (
      <ProtectedAdmin>
        <AdminOrdersPage />
      </ProtectedAdmin>
    ),
    errorElement: <ErrorPage />,
  },

  {
    path: "/login",
    element: <LoginPage />,
  },
  {
    path: "/signup",
    element: <SignupPage />,
  },
  {
    path: "/cart",
    element: (
      <Protected>
        <CartPage />
      </Protected>
    ),
  },
  {
    path: "/products/:id",
    element: (
      <Protected>
        <ProductDetailsPage />
      </Protected>
    ),
  },
  {
    path: "admin/products/:id",
    element: (
      <ProtectedAdmin>
        <AdminProductDetailsPage />
      </ProtectedAdmin>
    ),
  },
  {
    path: "/checkout",
    element: (
      <Protected>
        <CheckoutPage />
      </Protected>
    ),
  },
  {
    path: "/contact",
    element: (
      <Protected>
        <Contact />
      </Protected>
    ),
  },
  {
    path: "/about",
    element: (
      <Protected>
        <About />
      </Protected>
    ),
  },
  {
    path: "/order-success/:id",
    element: (
      <Protected>
        <OrderSuccessPage />
      </Protected>
    ),
  },
  {
    path: "/orders",
    element: (
      <Protected>
        <MyOrdersPage />
      </Protected>
    ),
  },
  {
    path: "/profile",
    element: (
      <Protected>
        <ProfilePage />
      </Protected>
    ),
  },
  {
    path: "/logOut",
    element: (
      <Protected>
        <LogOut />
      </Protected>
    ),
  },

  {
    path: "/forgotPassword",
    element: <ForgotPasswordPage />,
  },
  {
    path: "/resetPassword",
    element: <ResetPasswordPage />,
  },

  {
    path: "*",
    element: <ErrorPage />,
  },
]);
function App() {
  const user = useSelector(selectloggedInUserToken);
  const userChecked = useSelector(selectUserChecked);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(checkAuthAsync());
  }, [dispatch]);

  useEffect(() => {
    if (user) {
      dispatch(getCartItemsAsync());
      dispatch(fetchUserInfoAsync());
    }
  }, [dispatch, user]);
  return userChecked && <RouterProvider router={router} />;
}

export default App;
