export let totalItems = 0;

export function fetchProductList([pagination, sorting, filteration]) {
  if (sorting._sort === "id") {
    sorting = {};
  }
  let queryString = Object.entries({
    ...pagination,
    ...sorting,
  })
    .map(([key, value]) => `${key}=${value}`)
    .join("&");

  const filterOptions = { categories: [], brands: [] };

  for (let label in filteration) {
    for (let value of filteration[label]) {
      filterOptions[label === "category" ? "categories" : "brands"].push(value);
    }
  }

  let filterString = Object.entries(filterOptions)
    .map(([key, value]) =>
      value.length > 0 ? `${key}=${value.join(",")}` : ""
    )
    .join("&");

  return new Promise(async (resolve) => {
    const response = await fetch(
      `/api/products?${queryString}&${filterString}`,
      {
        credentials: "include",
      }
    );

    totalItems = response.headers.get("X-Total-Count");

    const data = await response.json();
    resolve({ data });
  });
}

export function fetchProductCategories() {
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/categories`, {
      credentials: "include",
    });
    const data = await response.json();
    resolve({ data });
  });
}

export function fetchProductBrands() {
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/brands`, {
      credentials: "include",
    });
    const data = await response.json();
    resolve({ data });
  });
}

export function fetchProductById(id) {
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/products/${id}`, {
      credentials: "include",
    });
    const data = await response.json();
    resolve({ data });
  });
}
export function addProduct(product) {
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/products`, {
      method: "POST",
      credentials: "include",
      body: JSON.stringify(product),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    resolve({ data });
  });
}

export function updateProduct(product) {
  return new Promise(async (resolve) => {
    const response = await fetch(`/api/products/${product.id}`, {
      method: "PATCH",
      credentials: "include",
      body: JSON.stringify(product),
      headers: { "content-type": "application/json" },
    });
    const data = await response.json();
    resolve({ data });
  });
}
