import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  checkAuth,
  loginUser,
  resetPassword,
  resetPasswordRequest,
} from "./authApi";
import { createUser, signOutUser } from "./authApi";
const initialState = {
  loggedInUserToken: null,
  status: "idle",
  error: null,
  userChecked: null,
  mailSent: false,
  passwordReset: false,
};

// The function below is called a thunk and allows us to perform async logic. It
// can be dispatched like a regular action: `dispatch(incrementAsync(10))`. This
// will call the thunk with the `dispatch` function as the first argument. Async
// code can then be executed and other actions can be dispatched. Thunks are
// typically used to make async requests.
export const createUserAsync = createAsyncThunk(
  "auth/createUser",
  async (userData) => {
    const response = await createUser(userData);
    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const loginUserAsync = createAsyncThunk(
  "auth/loginUser",
  async (loginInfo) => {
    const response = await loginUser(loginInfo);
    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const checkAuthAsync = createAsyncThunk("auth/checkAuth", async () => {
  const response = await checkAuth();
  //return created user data
  // The value we return becomes the `fulfilled` action payload
  return response.data;
});

export const signOutUserAsync = createAsyncThunk(
  "auth/signOutUser",
  async () => {
    const response = await signOutUser();
    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const resetPasswordRequestAsync = createAsyncThunk(
  "auth/resetPasswordRequest",
  async (recepientInfo) => {
    const response = await resetPasswordRequest(recepientInfo);
    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const resetPasswordAsync = createAsyncThunk(
  "auth/resetPassword",
  async (data) => {
    const response = await resetPassword(data);
    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const authSlice = createSlice({
  name: "auth",
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    increment: (state) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.value += 1;
    },
    resetStateError: (state) => {
      state.error = null;
      state.mailSent = false;
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(createUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(createUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = action.payload;
      })
      .addCase(loginUserAsync.pending, (state) => {
        state.status = "loading";
        state.userChecked = false;
      })
      .addCase(loginUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = action.payload;
        state.userChecked = true;
      })
      .addCase(loginUserAsync.rejected, (state, action) => {
        state.status = "idle";
        state.error = action.error;
        state.userChecked = true;
      })
      .addCase(resetPasswordRequestAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(resetPasswordRequestAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.mailSent = true;
      })
      .addCase(resetPasswordRequestAsync.rejected, (state, action) => {
        state.status = "idle";

        state.error = action.error;
        state.mailSent = false;
      })
      .addCase(resetPasswordAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(resetPasswordAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.passwordReset = true;
      })

      .addCase(checkAuthAsync.pending, (state) => {
        state.status = "loading";
        state.userChecked = false;
      })
      .addCase(checkAuthAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = action.payload;
        state.userChecked = true;
      })
      .addCase(checkAuthAsync.rejected, (state, action) => {
        state.status = "idle";
        state.userChecked = true;
      })
      .addCase(signOutUserAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(signOutUserAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.loggedInUserToken = null;
        state.userChecked = true;
      });
  },
});

export const { increment, resetStateError } = authSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
export const selectloggedInUserToken = (state) => state.auth.loggedInUserToken;
export const selectStateError = (state) => state.auth.error;
export const selectUserChecked = (state) => state.auth.userChecked;
export const selectMailSent = (state) => state.auth.mailSent;
export const selectPasswordReset = (state) => state.auth.passwordReset;
export default authSlice.reducer;
