import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import {
  addToCart,
  getCartItems,
  updateItemQuantity,
  removeItem,
  resetCart,
} from "./cartApi";

const initialState = {
  cartItems: [],
  status: "idle",
};

// The function below is called a thunk and allows us to perform async logic. It
// can be dispatched like a regular action: `dispatch(incrementAsync(10))`. This
// will call the thunk with the `dispatch` function as the first argument. Async
// code can then be executed and other actions can be dispatched. Thunks are
// typically used to make async requests.
export const addToCartAsync = createAsyncThunk(
  "cart/addToCart",
  async (item) => {
    const response = await addToCart(item);

    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const getCartItemsAsync = createAsyncThunk(
  "cart/getCartItems",
  async () => {
    const response = await getCartItems();

    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const updateItemQuantityAsync = createAsyncThunk(
  "cart/updateItemQuantity",
  async (item) => {
    const response = await updateItemQuantity(item);

    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);
export const removeItemAsync = createAsyncThunk(
  "cart/removeItem",
  async (item) => {
    const response = await removeItem(item);

    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const resetCartAsync = createAsyncThunk(
  "cart/resetCart",
  async (user) => {
    const response = await resetCart(user);

    //return created user data
    // The value we return becomes the `fulfilled` action payload
    return response.data;
  }
);

export const cartSlice = createSlice({
  name: "cart",
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    increment: (state) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.value += 1;
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(addToCartAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(addToCartAsync.fulfilled, (state, action) => {
        state.status = "idle";

        state.cartItems.push(action.payload);
      })
      .addCase(addToCartAsync.rejected, (state, action) => {
        state.status = "idle";
        alert(action.error.message);
      })
      .addCase(getCartItemsAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(getCartItemsAsync.fulfilled, (state, action) => {
        state.status = "idle";
        state.cartItems = action.payload;
      })
      .addCase(updateItemQuantityAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(updateItemQuantityAsync.fulfilled, (state, action) => {
        state.status = "idle";
        let index = state.cartItems.findIndex(
          (item) => item.id === action.payload.id
        );
        state.cartItems[index] = action.payload;
      })
      .addCase(removeItemAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(removeItemAsync.fulfilled, (state, action) => {
        state.status = "idle";
        let index = state.cartItems.findIndex(
          (item) => item.id === action.payload
        );

        state.cartItems.splice(index, 1);
      })
      .addCase(resetCartAsync.pending, (state) => {
        state.status = "loading";
      })
      .addCase(resetCartAsync.fulfilled, (state, action) => {
        state.status = "idle";

        state.cartItems = [];
      });
  },
});

export const { increment } = cartSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
export const selectCartItems = (state) => state.cart.cartItems;

export default cartSlice.reducer;
