import React, { useEffect } from "react";
import { useForm } from "react-hook-form";
import {
  addProductAsync,
  fetchProductByIdAsync,
  selectProductById,
  updateProductAsync,
} from "../../products/ProductListSlice";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";

function AdminProductForm() {
  const {
    register,
    handleSubmit,
    reset,
    setValue,
    formState: { errors },
  } = useForm();

  const params = useParams();
  const selectedProduct = useSelector(selectProductById);
  const dispatch = useDispatch();
  const onSubmit = (data) => {
    if (!params.id) {
      const image_url_list = data.images.split("\n");

      const product = { ...data, images: image_url_list };
      dispatch(addProductAsync(product));
    } else {
      const image_url_list = data.images.split("\n");
      const product = { ...data, images: image_url_list };
      //update product dipatch
      dispatch(updateProductAsync({ ...product, id: selectedProduct.id }));
    }
  };
  //useeffects are executed in order they appear
  useEffect(() => {
    if (params.id) {
      //does not block px-2 main thread
      dispatch(fetchProductByIdAsync(params.id));
    }
  }, [dispatch, params.id]);

  useEffect(() => {
    if (selectedProduct && params.id) {
      setValue("title", selectedProduct.title);
      setValue("description", selectedProduct.description);
      setValue("discountPercentage", selectedProduct.discountPercentage);
      setValue("price", selectedProduct.price);
      setValue("stock", selectedProduct.stock);
      setValue("rating", selectedProduct.rating);
      setValue("thumbnail", selectedProduct.thumbnail);
      setValue("brand", selectedProduct.brand);
      setValue("category", selectedProduct.category);
      setValue("images", selectedProduct.images.join("\n"));
    }
  }, [setValue, selectedProduct, reset, dispatch, params.id]);
  return (
    <form noValidate onSubmit={handleSubmit(onSubmit)}>
      <div className="space-y-12">
        <div className="pb-0">
          <div className="mt-10 grid grid-cols-1 gap-x-6 gap-y-8 sm:grid-cols-6">
            <div className="sm:col-span-3">
              <label
                htmlFor="title"
                className="block px-2 text-sm font-medium leading-6 text-gray-900"
              >
                Product Title
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="title"
                  id="title"
                  className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("title", {
                    required: "*Title is required",
                  })}
                />
                {errors.title && (
                  <p className="text-red-500">{errors.title.message}</p>
                )}
              </div>
            </div>

            <div className="sm:col-span-3">
              <label
                htmlFor="description"
                className="block px-2 text-sm font-medium leading-6 text-gray-900"
              >
                Description
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="description"
                  id="description"
                  className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("description", {
                    required: "*Description is required",
                  })}
                />
                {errors.description && (
                  <p className="text-red-500">{errors.description.message}</p>
                )}
              </div>
            </div>

            <div className="sm:col-span-2">
              <label
                htmlFor="price"
                className="block px-2 text-sm font-medium leading-6 text-gray-900"
              >
                Price
              </label>
              <div className="mt-2">
                <input
                  type="number"
                  name="price"
                  id="price"
                  className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("price", {
                    required: "*Price is required",
                  })}
                />
                {errors.price && (
                  <p className="text-red-500">{errors.price.message}</p>
                )}
              </div>
            </div>

            <div className="sm:col-span-2">
              <label
                htmlFor="discountPercentage"
                className="block px-2 text-sm font-medium leading-6 text-gray-900"
              >
                Discount Percentage
              </label>
              <div className="mt-2">
                <input
                  type="number"
                  name="discountPercentage"
                  id="discountPercentage"
                  className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("discountPercentage", {
                    required: "*Discount Percentage is required",
                  })}
                />
                {errors.discountPercentage && (
                  <p className="text-red-500">
                    {errors.discountPercentage.message}
                  </p>
                )}
              </div>
            </div>

            <div className="sm:col-span-2">
              <label
                htmlFor="rating"
                className="block px-2 text-sm font-medium leading-6 text-gray-900"
              >
                Rating
              </label>
              <div className="mt-2">
                <input
                  type="number"
                  name="rating"
                  id="rating"
                  className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("rating", {
                    required: "*Rating is required",
                  })}
                />
                {errors.rating && (
                  <p className="text-red-500">{errors.rating.message}</p>
                )}
              </div>
            </div>

            <div className="sm:col-span-2">
              <label
                htmlFor="stock"
                className="block px-2 text-sm font-medium leading-6 text-gray-900"
              >
                Stock
              </label>
              <div className="mt-2">
                <input
                  type="number"
                  name="stock"
                  id="stock"
                  className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("stock", {
                    required: "*Stock is required",
                  })}
                />
                {errors.stock && (
                  <p className="text-red-500">{errors.stock.message}</p>
                )}
              </div>
            </div>

            <div className="sm:col-span-2">
              <label
                htmlFor="brand"
                className="block px-2 text-sm font-medium leading-6 text-gray-900"
              >
                Brand
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="brand"
                  id="brand"
                  className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("brand", {
                    required: "*Brand is required",
                  })}
                />
                {errors.brand && (
                  <p className="text-red-500">{errors.brand.message}</p>
                )}
              </div>
            </div>

            <div className="sm:col-span-2">
              <label
                htmlFor="category"
                className="block px-2 text-sm font-medium leading-6 text-gray-900"
              >
                Category
              </label>
              <div className="mt-2">
                <input
                  type="text"
                  name="category"
                  id="category"
                  className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("category", {
                    required: "*Category is required",
                  })}
                />
                {errors.category && (
                  <p className="text-red-500">{errors.category.message}</p>
                )}
              </div>
            </div>

            <div className="sm:col-span-3">
              <label
                htmlFor="thumbnail"
                className="block px-2 text-sm font-medium leading-6 text-gray-900"
              >
                Thumbnail URL
              </label>
              <div className="mt-2">
                <input
                  type="url"
                  name="thumbnail"
                  id="thumbnail"
                  className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("thumbnail", {
                    required: "*Thumbnail URL is required",
                  })}
                />
                {errors.thumbnail && (
                  <p className="text-red-500">{errors.thumbnail.message}</p>
                )}
              </div>
            </div>

            <div className="sm:col-span-4">
              <label
                htmlFor="images"
                className="block px-2 text-sm font-medium leading-6 text-gray-900"
              >
                Image URLs
              </label>
              <div className="mt-2">
                <textarea
                  name="images"
                  id="images"
                  rows="5"
                  className="block px-2 w-full rounded-md border-0 py-1.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6"
                  {...register("images", {
                    required: "*Image URLs are required",
                  })}
                ></textarea>
                {errors.images && (
                  <p className="text-red-500">{errors.images.message}</p>
                )}
              </div>
            </div>

            {/* Add more fields for product information here */}
          </div>
          <div className="mt-10 flex items-center justify-end gap-x-6">
            <button
              type="button"
              onClick={() => reset()}
              className="text-sm font-semibold leading-6 text-gray-900"
            >
              Reset
            </button>

            <button
              type="submit"
              className="rounded-md bg-indigo-600 px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              {params.id ? "Save Changes" : "Add Product"}
            </button>
          </div>
        </div>
      </div>
    </form>
  );
}

export default AdminProductForm;
