const sortOptions = [
  { name: "Best Rating", current: false, key: "rating", order: "desc" },
  { name: "Price: Low to High", current: false, key: "price", order: "asc" },
  { name: "Price: High to Low", current: false, key: "price", order: "desc" },
];

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

export default function ProductList() {
  const [page, setPage] = useState(1);
  const [start, setStart] = useState(0);
  const [sortParams, setSortParams] = useState({ key: "id", order: "asc" });
  const [filterOptions, setFilterOptions] = useState({});
  const [mobileFiltersOpen, setMobileFiltersOpen] = useState(false);

  const dispatch = useDispatch();

  const products = useSelector(selectProducts);

  const brandList = useSelector(selectBrands);
  const categoryList = useSelector(selectCategories);

  const filters = [
    {
      id: "category",
      name: "Category",
      options: categoryList,
    },
    {
      id: "brand",
      name: "Brand",
      options: brandList,
    },
  ];

  function handleChange(e, section, option) {
    const newFilters = { ...filterOptions };

    if (!newFilters.hasOwnProperty(section.id)) {
      newFilters[section.id] = [];
    }

    if (e.target.checked) {
      newFilters[section.id].push(option);
    } else {
      newFilters[section.id].splice(newFilters[section.id].indexOf(option), 1);
    }

    setFilterOptions(newFilters);
    setStart(0);
    setPage(1);
  }

  useEffect(() => {
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE };
    const sorting = { _sort: sortParams.key, _order: sortParams.order };
    const filteration = filterOptions;
    dispatch(fetchProductListAsync([pagination, sorting, filteration]));
  }, [dispatch, page, sortParams, filterOptions]);

  useEffect(() => {
    dispatch(fetchProductBrandsAsync());
    dispatch(fetchProductCategoriesAsync());
  }, [dispatch]);

  return (
    <div className="bg-white">
      <div>
        {/* Mobile filter dialog */}
        <MobileFilterDialog
          filters={filters}
          handleChange={handleChange}
          filterOptions={filterOptions}
          setFilterOptions={setFilterOptions}
          mobileFiltersOpen={mobileFiltersOpen}
          setMobileFiltersOpen={setMobileFiltersOpen}
        />

        <main className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
          <div className="flex items-baseline justify-between border-b border-gray-200 pb-6 pt-6">
            <h1 className="text-4xl font-bold tracking-tight text-gray-900">
              Our Products
            </h1>

            <SortOptions
              start={start}
              setStart={setStart}
              page={page}
              setPage={setPage}
              sortParams={sortParams}
              setSortParams={setSortParams}
              mobileFiltersOpen={mobileFiltersOpen}
              setMobileFiltersOpen={setMobileFiltersOpen}
            />
          </div>

          <ProductGrid
            filters={filters}
            start={start}
            setStart={setStart}
            page={page}
            setPage={setPage}
            products={products}
            filterOptions={filterOptions}
            setFilterOptions={setFilterOptions}
            handleChange={handleChange}
          />

          {/* {productGridEnd} */}

          <Pagination
            page={page}
            setPage={setPage}
            start={start}
            setStart={setStart}
            totalItems={totalItems}
          />
        </main>
      </div>
    </div>
  );
}

//components in productList

